.NavBar{
  height:68px;

  background-color:transparent;
  color: white;
  position:fixed;
  width: 90%;
  backdrop-filter: blur(28px);
  z-index: 12;
  border: 2px solid #ff01fb;
  border-radius: 999px;
  margin-top: 1rem;
}
.navcon1{

  width: 250px;
  padding:4px;
}
.navcon2{

  width: 900px;
}
.navcon3{

  width: 150px;
}

.btnsnav {
  align-items: center;
  background-image: linear-gradient(144deg,#AF40FF, #5B42F3 50%,#00DDEB);
  border: 0;
  border-radius: 8px;
  box-shadow: rgba(151, 65, 252, 0.2) 0 15px 30px -5px;
  box-sizing: border-box;
  color: #FFFFFF;
  display: flex;
  font-family: Phantomsans, sans-serif;
  font-size: 20px;
  justify-content: center;
  line-height: 1em;
  padding: 3px;
  text-decoration: none;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
  white-space: nowrap;
  cursor: pointer;
  width: 112px;
  height: 43px;
  border-radius: 30px;
}

.btnsnav:active,
.btnsnav:hover {
  outline: 0;
}

.btnsnav .btnnavtext {
  background-color: rgb(5, 6, 45);
  padding: 8px 22px;
  border-radius: 6px;
  width: 100%;
  height: 100%;
  transition: 300ms;
  border-radius: 30px;
}

.btnsnav:hover .btnnavtext {
  background: none;
}
